.card-component img {
  width: 100%;
}

.cardTitle {
  width: 100%;
  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 28px;
  padding: 10px 0;
  line-height: 39px;
  text-align: center;
  color: #e6cbae;
}
@media only screen and (max-width: 1024px) {
  .card-component {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .card-component img {
    width: 80%;
    padding: 1.4rem 0;
  }
}
