.oval img {
  height: 450px;
  width: 300px;
  border-radius: 149px/215px;
  border: 1px solid #000000;
}
.aboutme-welcome {
  background: url("../../assets/mainBack.png") no-repeat;
  height: 120vh;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.aboutMe-wrapper {
  display: flex;
  width: 60vw;
}

.aboutMe {
  width: 35vw;
  padding-top: 30px;
  padding-left: 60px;
}
.aboutMe-title {
  font-family: "Averia Serif Libre";
  font-style: normal;
  font-weight: 400;
  font-size: 56px;
  line-height: 68px;
  /* identical to box height */
  padding-bottom: 30px;
  text-align: left;

  color: #000000;
}
.aboutMe-text {
  font-family: "Average";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 148.52%;
  /* or 30px */

  color: #000000;
}
