.welcome {
  background: url("../../assets/backgroundWith.png") no-repeat;
  background-size: 101%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.welcomeText {
  width: 30vw;
}
.welcomeTextContainer {
  text-align: center;
}
.welcomeHeader {
  font-family: "Averia Serif Libre";
  font-style: normal;
  font-weight: 400;
  font-size: 56px;
  line-height: 68px;
  text-align: center;
  letter-spacing: 0.02em;
  color: #917196;
}
.welcomeFooter {
  margin-top: 5vh;
  font-family: "Average";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 148.52%;
  text-align: center;
  color: #000000;
}
.welcomeTextContainer button {
  background-color: #917196;
  color: white;
  margin-top: 6vh;
  height: 47px;
  width: 15vw;
  border-radius: 25px;
}

@media only screen and (max-width: 1024px) {
  .welcome {
    height: auto;
    width: 100%;
    background: url("../../assets/mainBack.png") no-repeat;
    background-size: 101%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .welcomeText {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .welcomeTextContainer {
    height: 90vh;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
  .welcomeTextContainer button {
    background-color: #917196;
    color: white;
    margin-top: 8vh;
    height: 47px;
    width: 200px;
    border-radius: 25px;
  }
}
