.whatIDo {
  background: url("../../assets/rightQualif.svg");
  height: 80vh;
  background-repeat: no-repeat;
  background-size: 330px;
  background-position: right;
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 50px;
}
.whatIDo-title {
  font-family: "Averia Serif Libre";
  font-style: normal;
  font-weight: 400;
  font-size: 56px;
  line-height: 68px;
  /* identical to box height */
  text-align: center;
  letter-spacing: 0.02em;

  color: #917196;
}

.whatIDo-body {
  max-width: 30vw;
  padding: 10px;
  font-family: "Average";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 148.52%;
  /* or 48px */

  color: #000000;
}
