:root {
	--mainColor: #917196;
	--mainColorLight: #5767aa;
	--secondaryColor: #db2b39;
	--textColor: #ffff;
}

header {
	display: flex;
	justify-content: space-around;
	height: 80px;
	padding: 0 3rem;
	background-color: var(--mainColor);
	color: var(--textColor);
}

nav div {

	color: var(--textColor);
	text-decoration: none;
}

nav div:hover {
	color: var(--secondaryColor);
}
nav{
    width: 100%;
    justify-content: end;
	gap:1.9rem;
}

header .nav-btn {
	padding: 5px;
	cursor: pointer;
	border: none;
	outline: none;
	color: white;
    background-color: #917196;
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}

header div,
nav {
    
	display: flex;
	align-items: center;
}
.logo{
   padding-left:10vw;
}

@media only screen and (max-width: 1024px) {
	header .nav-btn {
		visibility: visible;
		opacity: 1;
	}
.logo{
   padding-left:1vw;
   width: 100%;

}
	header nav {
		position: fixed;
		top: -100vh;
        z-index: 1000000000;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: var(--mainColor);
		transition: 1s;
	}

	header .responsive_nav {
		transform: translateY(100vh);
	}

	nav .nav-close-btn {
		position: absolute;
		top: 1.5rem;
		right: 3rem;
	}
   
	nav div {
		font-size: 1.5rem;
    
	}
}