.footer {
  background: url("../../assets/footerBack.png");
  width: 100%;
  height: 310px;
  background-size: cover;
  display: flex;
  justify-content: space-between;
  padding: 100px 50px;
}
.socials {
  font-family: "Monda";
  font-style: normal;
  font-weight: 400;
  font-size: 23px;
  line-height: 33px;
  /* identical to box height */

  text-transform: capitalize;

  color: #ffffff;
}
.follow-icons {
  display: flex;
}
.follow-icons img {
  padding-top: 5px;
  padding-right: 15px;
}

.footer-btn {
  font-family: "Average";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;

  height: 50px;
  color: #d6809b;
  background: #faf5ef;
  border-radius: 37px;
  width: 150px;
}

.further-text {
  padding-bottom: 5px;

  font-family: "Monda";
  color: #ffffff;
  font-style: normal;
  font-weight: 400;
  text-transform: capitalize;
  font-size: 23px;
}
@media only screen and (max-width: 1024px) {
  .footer {
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
  }
}
